@use "/src/assets/styles/color.scss" as color;
@use "/src/assets/styles/sizes.scss" as sizes;
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&family=Red+Hat+Mono:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400;1,500&display=swap");

:root {
  @each $color, $types in color.$colors {
    @each $type, $value in $types {
      --clr-#{$color}-#{$type}: #{$value};
    }
  }
  --HomePageItems: $HomepageItem;
  --DetailPage: $DetailPage;
}
html {
  body {
    margin: 0px;
    margin: 0px;
    font-family: "Nunito Sans", sans-serif;
    // font-family: 'Red Hat Mono', monospace;
  }
}

* {
  box-sizing: border-box;
  // outline: 1px solid limegreen !important;
}
